import React, {useState} from "react"
import { Navbar, Nav, Modal, Button } from "react-bootstrap"
import { Link } from 'gatsby'
// https://www.youtube.com/watch?v=WPPPFqsECz0

export default function Navigation() {

	const [show, setShow] = useState(false);

	const handleClose = () => setShow(false);
	const handleShow = () => setShow(true);


	return (
		<div>
			<Navbar bg="light" expand="lg">
				<Navbar.Brand to="/" as={Link}>Giving Tree</Navbar.Brand>
				<Navbar.Toggle aria-controls="basic-navbar-nav" />
				<Navbar.Collapse id="basic-navbar-nav">
					<Nav className="mr-auto">
						<Nav.Item as={Button} onClick={handleShow}>About</Nav.Item>
					</Nav>
				</Navbar.Collapse>
			</Navbar>
			<Modal show={show} onHide={handleClose}>
				<Modal.Header closeButton>
					<Modal.Title>Welcome to the Giving Tree</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					The purpose of this site is to give you space to reflect on the
					positive things that have happened. Sometimes we focus on the people
					around us and their sucesses, but you are amazing! So here's to understanding
					how awesome you are! Inspired by this 
					<a href="https://www.youtube.com/watch?v=WPPPFqsECz0"> video on gratitude</a>.
				</Modal.Body>
				<Modal.Footer>
					<Button variant="secondary" onClick={handleClose}>
						Close
					</Button>
				</Modal.Footer>
			</Modal>
		</div>
	)
}
