import React from "react"
import styled, { keyframes } from 'styled-components'
import Apple from "../../static/apple.svg"

import styles from "../components/plant.module.css"

const bounceAnimation = keyframes`
	{
        0%   { transform: scale(1,1)      translateY(-40px); }
        10%  { transform: scale(1.1,.9)   translateY(-40px); }
        30%  { transform: scale(.9,1.1)   translateY(-140px); }
        50%  { transform: scale(1.05,.95) translateY(-40px); }
        57%  { transform: scale(1,1)      translateY(-47px); }
        64%  { transform: scale(1,1)      translateY(-40px); }
        100% { transform: scale(1,1)      translateY(-40px); }
    }
`;
 
const BouncyDiv = styled.div`
	position: absolute;
    height: 120px;
    width: 100px;
    margin: auto;
    transform: translateY(-40px);
    animation: ${bounceAnimation} 3s infinite;
`;

class Plant extends React.Component {
	constructor(props) {
		super();
	}

	render() {
		return (
			<div className={styles.screen}>
				<div className={styles.center}>
					<BouncyDiv><Apple /></BouncyDiv>
				</div>
			</div>
	  	)
	}
}

export default Plant
